
import { onMounted, onUpdated, ref, shallowRef } from 'vue';
// import { ShowOnScroll } from '@/functions/show-on-scroll';
import { store } from '@/store';
import { useRoute, useRouter } from 'vue-router';

export default {
  props: {
    galleryIndex: Number,
    imageSourceFull: String,
    imageSourcePreview: String,
    imageSourceRatio: Number,
    visible: Boolean,
  },
  setup(props) {
    const route = useRoute();
    const router = useRouter();

    const imageLoader = shallowRef<Element | null>();
    // const imageLoaderWrapper = ref<Element | null>();
    const imageDisplayContainer = shallowRef<Element | null>();
    const imageLoadState = ref<'notLoaded' | 'previewLoaded'>('notLoaded');

    const instantImageLoad = ref(false);

    const imageLoadStartTime = Date.now();
    const image = new Image();
    image.src = props.imageSourcePreview!;
    image.onload = () => {
      imageLoadState.value = 'previewLoaded';

      // if (Date.now() - imageLoadStartTime > 5) {
      //   instantImageLoad.value = true;
      // }

      // const fullImage = new Image();
      // fullImage.src = props.imageSourceFull!;
      // fullImage.onload = () => {
      //   imageLoadState.value = 'fullLoaded';
      // };
    };

    function onImageClick() {
      store.state.portfolioView.currentGalleryIndex = props.galleryIndex!;
      store.state.navigationBar.isVisible = true;
      store.state.navigationBar.transparent = false;
      console.log(store.state.portfolioView.currentGalleryIndex);

      router.push({ name: route.name!, query: { ...route.query, index: props.galleryIndex } });
    }
    // onMounted(() => {
    //   new ShowOnScroll({
    //     selector: '.gallery-image-wrapper',
    //     visibleClassName: 'show-on-scroll-visible',
    //     // applyOnChildElemetSelector: "a",
    //   });
    // });

    onUpdated(() => {
      // console.log(props.visible);
      // if (!props.hideIndexesList) {
      //   (imageDisplayContainer.value as HTMLElement).style.opacity = '1';
      //   return;
      // }
      // for (const iterator of props.hideIndexesList) {
      //   if (iterator === props.galleryIndex) {
      //     //hide self
      //     if (props.imageSourceRatio?.toString().includes('nudes')) {
      //       console.log('a');
      //     }
      //     (imageDisplayContainer.value as HTMLElement).style.opacity = '0';
      //     return;
      //   }
      // }
      if (props.visible) {
        (imageDisplayContainer.value as HTMLElement).style.opacity = '1';
      } else {
        (imageDisplayContainer.value as HTMLElement).style.opacity = '0';
      }
    });

    onMounted(async () => {
      let containerWidth;
      if (store.state.currentBrowser !== 'firefox') {
        containerWidth = (imageDisplayContainer.value as HTMLElement).offsetWidth;
      } else {
        containerWidth = 150;
        return;
      }
      // const containerWidth = 150;
      // const containerWidth = imageDisplayContainer.value?.getBoundingClientRect().width ?? 300;
      //   console.log(imageDisplayContainer.value);
      (imageLoader.value as HTMLElement).style.height = `${
        containerWidth / props.imageSourceRatio! ?? containerWidth
      }px`;

      // console.log(
      //   `wrapper w: ${containerWidth}, image ratio: ${props.imageSourceRatio}, new wrapper h: ${
      //     containerWidth / props.imageSourceRatio!
      //   }`
      // );
      // (imageLoader.value as HTMLElement).style.height = `${
      //   containerWidth / props.imageSourceRatio! ?? containerWidth
      // }px`;
    });

    return {
      imageLoadState,
      imageDisplayContainer,
      imageLoader,
      onImageClick,
      instantImageLoad,
      store,
    };
  },
};
